import React, {useRef, useState, useEffect} from 'react'   
import * as PIXI from 'pixi.js'
import { Stage, Text, Container, Graphics, Sprite } from '@pixi/react';
import {gsap} from 'gsap';
import { PixiPlugin } from "gsap/PixiPlugin";
import './Aviator.css';
import RotatingSun from './components/RotatingSun';
import RotatingProp from './components/RotatingProp';
import ReducingProgressBar from './components/ReducingProgressBar';
import FlyingPlane from './components/FlyingPlane';
import BlurBg from './components/Blurbg';
import { HorizontalLineCreation, VerticalLineCreation } from './components/Axis';
import AnimateQuadraticCurve from './components/QuadraticCurve';

// Registring GSAP Pixi plugin 
gsap.registerPlugin(PixiPlugin);

const Aviator = () =>{
    
    const myDivRef = useRef(null);
    const [width, setWidth] = useState(0);
    const [height, setHeight] = useState(0);
    // 0 -> wait, 1 -> prep, 2 -> start, 3 -> end 
    const [gameStatus, setGameStatus] = useState(0);

    const [fontColor, setFontColor] = useState("#D0021B");
    const handleFontColor = (color) =>{
        setFontColor(color)
    }

    useEffect(() =>{
        if(gameStatus === 2){
            handleFontColor("#FFFFFF");
        }
        if(gameStatus === 3){
            handleFontColor("#D0021B");
        }
    },[gameStatus]);

    // Use useEffect to log the width and height after the component has rendered
    const textStyle = new PIXI.TextStyle({
        align : 'center', 
        fill: "#FFFFFF",
        letterSpacing: 3, 
        fontSize: Math.min(width, height) * 0.06, 
        // fontWeight: 'bold'
    })

    const endtextStyle = new PIXI.TextStyle({
        // fontFamily: 'TrimWeb',
        align : 'center', 
        fill: fontColor,
        letterSpacing: 3, 
        fontSize: Math.min(width, height) * 0.2, 
        fontWeight: 'bold',
      })
    /**
     * Responsiveness of canvas is handled here and Methods  -- Start
     */

    useEffect(() => {
        // Access the DOM element using the ref
        const myDiv = myDivRef.current;
        // Check if the ref is defined (component may not be mounted yet)
        if (myDiv) {
            const divWidth = myDiv.offsetWidth;
            const divHeight = myDiv.offsetHeight;
            setWidth(divWidth);
            setHeight(divHeight);
        }
        }, []);

    useEffect(() => {
        const handleResize = () => {
        const myDiv = myDivRef.current;
            if (myDiv) {
                const divWidth = myDiv.offsetWidth;
                const divHeight = myDiv.offsetHeight;
                setWidth(divWidth);
                setHeight(divHeight);
            }
        };

        window.addEventListener('resize', handleResize);

        // Clean up event listener on unmount
        return () => {
        window.removeEventListener('resize', handleResize);
        };
    }, []);
   
    /**
     * Responsiveness of canvas is handled here and Methods -- End
     */

    
    const barWidth = width*0.35;
    const barHeight = height*0.02;
    const lineLength = 10; // Length of each line segment
    const gapLength = 2; // Length of each gap between line segments

    /**Automating Game Status Handling -- Start */
    useEffect(()=>{
        const handleGameStatusChange = (newStatus) => {
            setGameStatus(newStatus);
        };

        let timer;
        
        const startTimer = (newStatus, duration) => {
            timer = setTimeout(() => {
                handleGameStatusChange(newStatus);
            }, duration);
        };

        const resetTimer = (newStatus, duration) => {
            clearInterval(timer);
            startTimer(newStatus, duration);
        };

        
        if (gameStatus === 0) {
            startTimer(1, 5000);
        } else if (gameStatus === 1) {
            startTimer(2, 2000);
        } else if (gameStatus === 2) {
        // Adjust 'x' to the desired duration for playing to end transition
            startTimer(3, 20000);
        } else if (gameStatus === 3) {
        // Adjust 'x' to the desired duration for end to start transition
            resetTimer(0, 2000);
        }
        
        return () => {
            clearInterval(timer)
        };

    },[gameStatus]);
    /**Automating Game Status Handling -- End */
   
    /** Demo Multiplier value counter logic - Start */
    const [counter, setCounter] = useState(1);
    
    useEffect(() => {
        let interval;
        
        if (gameStatus === 2) {
            setCounter(1); // Reset the counter when gameStatus changes to 2
            interval = setInterval(() => {
                setCounter((prevCounter) => {
                    // Generate a random number within the range [0.01, 0.1]
                    const randomIncrement = Math.random() * (0.1 - 0.01) + 0.1;
                    // Increase the counter by the random increment
                    return prevCounter + randomIncrement;
                });
            }, 200); // 2000 milliseconds = 2 seconds
        }
        
        return () => clearInterval(interval);
    }, [gameStatus]);
    
    /** Demo Multiplier value counter logic - End */

    return(
        <>
            <div  className='stage-canvas-div'   ref={myDivRef}  >
                <Stage className='stage-canvas'  width={width} height={height} options={{backgroundColor: 0x000000}} >
                    <Container position={[0, height]} >
                        <RotatingSun play={gameStatus === 2} />
                    </Container>
                    {(gameStatus == 1 || gameStatus == 0) &&
                        <FlyingPlane width={width} height={height} />
                    }
                    {/* Waiting scene start */}
                    {gameStatus === 0 && 
                    <>
                        <Container position={[width*0.5, height*0.5]}>   
                            <Text text="WAITING FOR NEXT ROUND" anchor={[0.5, 0.5]} style={textStyle} />
                        </Container>
                        <RotatingProp width={width} height={height} />
                        <ReducingProgressBar 
                        barWidth={barWidth}
                        barHeight={barHeight}
                        width={width}
                        height={height}
                        durationInSeconds={5}

                        />
                    </>
                    }
                   
                   
                        {/* Game start scene */}

                        {gameStatus === 2 &&
                            <>
                                <Container position={[width*0.5 , height*0.5]} >
                                    <BlurBg multiplierValue={counter.toFixed(2)} /> 
                                </Container>
                                {/* <VerticalDottedLineCreationAndAnimation width={width} height={height} startAnimation={true}/>
                                <HorizontalDottedLineCreationAndAnimation width={width} height={height} /> */}
                                <HorizontalLineCreation width={width} height={height} />
                                <VerticalLineCreation width={width} height={height} />
                                <AnimateQuadraticCurve width={width} height={height} startAnimation={gameStatus===2} />
                                
                            </> 
                         }

                        {/* Game end scene */} 

                        {gameStatus === 3 &&
                        <>
                        <Container position={[width*0.5, height*0.35]}>   
                            <Text text="FLEW AWAY!" anchor={[0.5, 0.5]} style={textStyle} />
                        </Container>
                        </>
                        }

                        {(gameStatus === 3 || gameStatus === 2) &&
                        <Container position={[width*0.5, height*0.5]}>   
                            <Text text={`${counter.toFixed(2)} x`} anchor={[0.5, 0.5]} style={endtextStyle} />
                        </Container>
                        }
                          {/* <Graphics
                            draw={g => {
                                g.lineStyle(2, 0x281F20, 1); // lineStyle(thickness, color, alpha)
                                g.moveTo(width*0.01, height*0.97); // start point
                                g.lineTo(width, height*0.97); // end point
                            }}
                            />

                        <Graphics
                            draw={g => {
                                g.lineStyle(2, 0x281F20, 1); // lineStyle(thickness, color, alpha)
                                g.moveTo(width*0.01, height*0.97); // start point
                                g.lineTo(width*0.01, 0); // end point
                            }}
                        /> */}
                    
                       
              
                </Stage>

                {/* // 0 -> wait, 1 -> prep, 2 -> start, 3 -> end  */}
                {/* <button onClick={()=>setGameStatus(0)}  >Wait Scene</button>
                <button onClick={()=>setGameStatus(1)}  >Prep Scene</button>
                <button  onClick={()=>setGameStatus(2)}  >Start Scene</button>
                <button onClick={()=>setGameStatus(3)} >End Scene</button> */}
            </div>

        </> 
    );
}


export default Aviator;