import React, { useRef, useState, useEffect, useCallback } from "react";
import gsap from "gsap";
import * as PIXI from "pixi.js";
import { Graphics, Stage } from "@pixi/react";

const QuadCurve = () => {
  const rectRef = useRef(null);
  const CONTROL_POINT_X_PERCENT = 0.5;
  const MOUNT_TIME = 8;
  const GLIDE_TIME = 4;
  const AXIS_OFFSET = 30;
  const MOUNT_POINT_OFFSET_PLANE_SCALE = { x: 1.5, y: 1 };
  const CURVE_WIDTH = 4;
  const CURVE_COLOR = "#f00b3e";
  const GRADIENT_COLOR = "#d0021b";
  const [width, setWidth] = useState(768);
  const [height, setHeight] = useState(400);

  const startPoint = { x: AXIS_OFFSET, y: height - AXIS_OFFSET };
  const controlPoint = { x: 0, y: 0 };
  const [endPoint, setEndPoint] = useState({
    x: AXIS_OFFSET,
    y: height - AXIS_OFFSET,
  });
  const plane = { width: 140, height: 90 };
  const mountPoint = {
    x: Math.max(
      width - MOUNT_POINT_OFFSET_PLANE_SCALE.x * plane.width,
      startPoint.x
    ),
    y: Math.min(MOUNT_POINT_OFFSET_PLANE_SCALE.y * plane.height, startPoint.y),
  };
  const glidePoint = { x: 0, y: 0 };

  const drawCurve = useCallback(
    (g) => {
      if (!g) return;

      controlPoint.x = CONTROL_POINT_X_PERCENT * endPoint.x;
      controlPoint.y = startPoint.y;
      g.clear();
      g.lineStyle({
        width: CURVE_WIDTH,
        color: CURVE_COLOR,
        join: PIXI.LINE_JOIN.ROUND,
        // cap: PIXI.LINE_CAP.ROUND,
        alignment: 0.5,
      })
        .moveTo(startPoint.x, startPoint.y)
        .quadraticCurveTo(
          controlPoint.x,
          controlPoint.y,
          endPoint.x,
          endPoint.y
        );
      g.lineStyle();
      g.beginFill(GRADIENT_COLOR, 0.5)
        .moveTo(startPoint.x, startPoint.y)
        .quadraticCurveTo(
          controlPoint.x,
          controlPoint.y,
          endPoint.x,
          endPoint.y
        )
        .lineTo(endPoint.x, startPoint.y)
        .lineTo(startPoint.x, startPoint.y);
    },
    [endPoint, startPoint, controlPoint]
  );

  const playAnimation = () => {
    tl.play();
  };

  const pauseAnimation = () => {
    tl.pause();
  };

  const restartAnimation = () => {
    tl.restart();
  };

  const revertAnimation = () => {
    tl.reverse();
  };

  const [tl] = useState(() =>
    gsap.timeline({
      paused: true,
      onComplete: () => console.log("play axis"),
    })
  );

  useEffect(() => {
    tl.to(
      endPoint,
      {
        x: mountPoint.x,
        ease: "power1.out",
        duration: MOUNT_TIME,
        onStart: () => {},
        onUpdate: () => {
          setEndPoint((prev) => ({
            ...prev,
            x: gsap.getProperty(endPoint, "x"),
          }));
        },
      },
      0
    ).to(
      endPoint,
      {
        y: mountPoint.y,
        ease: "power1.inOut",
        duration: MOUNT_TIME,
        onUpdate: () => {
          setEndPoint((prev) => ({
            ...prev,
            y: gsap.getProperty(endPoint, "y"),
          }));
        },
      },
      0
    );
  }, []);

  const clearCanvas = () => {
    console.log("canvas cleared.");
  };

  const redrawGraphics = () => {
    console.log("redraw graph");
  };

  function debounce(func, wait) {
    let timeout;

    return function (...args) {
      const context = this;
      clearTimeout(timeout);

      timeout = setTimeout(() => {
        func.apply(context, args);
      }, wait);
    };
  }

  const handleResize = debounce(playAnimation, 2000);

  window.addEventListener("resize", handleResize);

  return (
    <>
      <h1>Quadratic Curves</h1>
      <Stage
        width={width}
        height={height}
        options={{
          backgroundColor: 0xffffff,
          antialias: true,
          resolution: window.devicePixelRatio || 1,
        }}
      >
        <Graphics ref={rectRef} draw={drawCurve} />
      </Stage>
      <br />
      <button onClick={playAnimation}>Play</button>
      <button onClick={pauseAnimation}>Pause</button>
      <button onClick={restartAnimation}>Restart</button>
      <button onClick={revertAnimation}>Revert</button>
      {/* <button onClick={clearCanvas}>clearGraphics</button>
      <button onClick={redrawGraphics}>Redraw</button> */}
    </>
  );
};

export default QuadCurve;
