import React, {useRef, useState, useLayoutEffect, useEffect} from 'react'   
import * as PIXI from 'pixi.js'
import { useTick, AnimatedSprite, Container } from '@pixi/react';
import plane from '../assets/prop.svg'
import {flyPlaneFrames } from '../assets/plane';


/** Flying Rocket Animation Sequence */
const flyPlaneTextureArray = flyPlaneFrames.map((value)=>{ 
  const texture = PIXI.Texture.from(value);
  return texture;

})

const planegetScale = (width, height) => {
  const scale = Math.min(width, height) / 1000;
  return Math.max(0.7, Math.min(scale, 1));
};


const roundValues =(val)=>{
  return Math.ceil(val);
}


const FlyingPlane = ({width, height}) => {



  return (
 
        <AnimatedSprite
          anchor={[0,1]}
          position={[width*0.04, height*0.95]}
          scale={planegetScale(width, height)}
          textures={flyPlaneTextureArray}
          isPlaying={true}
          initialFrame={0}
          loop={true}
          animationSpeed={0.2}
        />
 
  );
};


  export default FlyingPlane;
  