import React, { useEffect, useRef, useMemo, useState } from 'react';
import gsap from 'gsap';
import * as PIXI from 'pixi.js';
import { Graphics, AnimatedSprite } from '@pixi/react';
import { flyPlaneFrames } from '../assets/plane';
import { HorizontalDottedLineCreationAndAnimation, VerticalDottedLineCreationAndAnimation, HorizontalLineCreation, VerticalLineCreation } from './Axis';

const flyPlaneTextureArray = flyPlaneFrames.map(value => PIXI.Texture.from(value));

const planegetScale = (width, height) => {
  const scale = Math.min(width, height) / 1000;
  return Math.max(0.7, Math.min(scale, 1));  
};

const AnimateQuadraticCurve = ({ width, height, startAnimation }) => {
  const graphicsRef = useRef(null);
  const planeRef = useRef(null);
  const [startDottedLineAnimation, setStartDottedLineAnimation] = useState(false);

  const [endPoint, setEndPoint] = useState({ x: width * 0.04, y: height * 0.95 });
  const glideEndPoint = { x: width * 0.65, y: height * 0.05 };
  const glideStartPoint = { x: width * 0.75, y: height * 0.9 };
  useEffect(() => {
    const graphics = graphicsRef.current;
    const plane = planeRef.current;

    if (!graphics) return;

    const animationDuration = 8;
    const startPoint = { x: width * 0.04, y: height * 0.95 };
    console.time('QuadCurve')
    const animateQuadraticCurve = () => {
      const tl = gsap.timeline();
      tl.to(endPoint, {
        duration: animationDuration,
        x: width * 0.65,
        y: height * 0.25,
        // ease: "power1.inOut",
        onUpdate: () => {
          drawQuadraticCurve(graphics, startPoint, calculateControlPoint(startPoint, endPoint), endPoint);
        },
        onComplete: () => {
          // Animation complete callback
          console.log('QuadCurve Animation Complete')
          setStartDottedLineAnimation(true);
      
        },
      }).add(()=>{
        gsap.to(endPoint,{
          duration: 4,
          x: glideStartPoint.x,
          y: glideStartPoint.y,
          ease: "sine.inOut",
          yoyo: true,
          repeat: -1,
          onUpdate: () => {
            drawQuadraticCurve(graphics, startPoint, calculateControlPoint(startPoint, endPoint), endPoint);
          },
        })
      });


    };
    
    console.timeEnd('QuadCurve')

    animateQuadraticCurve();
    // if (startAnimation) {
    // } else {
    //   gsap.killTweensOf(endPoint);
    // }

    return () => {
      gsap.killTweensOf(endPoint);
    };

  }, [width, height, startAnimation, endPoint, glideStartPoint.x,glideStartPoint.y]);

  useEffect(() => {
    const plane = planeRef.current;
    if (!plane) return; // If plane is null, exit early

    // Set initial position of the plane
    plane.x = width * 0.04;
    plane.y = height * 0.95;

    const animationDuration = 8;

    const animateFlyingPlane = () => {
      const tl = gsap.timeline({ repeat: 0, yoyo: false });
      tl.to(plane, {
        duration: animationDuration,
        x: width * 0.65,
        y: height * 0.25,
        // ease: "power1.inOut",
      }).add(()=>{
        gsap.to(plane,{

          x: glideStartPoint.x,
          y: glideStartPoint.y,
          duration: 4, 
          ease: "sine.inOut",
          yoyo: true,
          repeat: -1
        })
      })
      // .add(()=>{
      //   gsap.to(plane, {
      //     duration : .5,
      //     x: width + plane.width,
      //     y: plane.y -200, 
      //     ease: 'none'
      //   })
      // })
      ;
    };

      animateFlyingPlane();
    // if (startAnimation) {
    // } else {
    //   gsap.killTweensOf(plane);
    // }

    return () => {
      gsap.killTweensOf(plane);
    };

  }, [width, height, startAnimation]);

  const drawQuadraticCurve = (graphics, startPoint, controlPoint, endPoint) => {
    graphics.clear();
    graphics.lineStyle({
      width: 4,
      color: 0xF00B3E, // Line color
      cap: PIXI.LINE_CAP.ROUND, // Rounded line caps for smoothness
      alignment: 0.5, // Alignment of the line (0.5 centers the line on the vertices)
    });
    graphics.moveTo(startPoint.x, startPoint.y);
    graphics.quadraticCurveTo(controlPoint.x, controlPoint.y, endPoint.x, endPoint.y);
    graphics.lineStyle();
    graphics.beginFill(0xF00B3E, 0.5);
    graphics.moveTo(startPoint.x, startPoint.y);
    graphics.quadraticCurveTo(controlPoint.x, controlPoint.y, endPoint.x, endPoint.y);
    graphics.lineTo(endPoint.x, startPoint.y);
    graphics.lineTo(startPoint.x, startPoint.y);
    graphics.endFill();
  };

  const calculateControlPoint = useMemo(() => {
    return (startPoint, endPoint) => ({ x: startPoint.x + (endPoint.x - startPoint.x) / 2, y: startPoint.y });
  }, []);

  return (
    <>
      <AnimatedSprite
        ref={planeRef}
        anchor={[0.1, 0.9]}
        scale={planegetScale(width, height)}
        textures={flyPlaneTextureArray}
        isPlaying={true}
        initialFrame={0}
        loop={true}
        animationSpeed={0.2}
        // x={width * 0.04}
        // y={height * 0.95}
      />
      <Graphics ref={graphicsRef} />
      <VerticalDottedLineCreationAndAnimation width={width} height={height} startAnimation={startDottedLineAnimation}/>
      <HorizontalDottedLineCreationAndAnimation width={width} height={height} startAnimation={startDottedLineAnimation} />
    </>
  );
};

export default AnimateQuadraticCurve;
